<template>
  <div class="twoke">
    <div :class="config?'fixedClass':''">
      <van-nav-bar left-text="返回" left-arrow @click-left="onClickLeft">
        <template #title>
          <div v-show="!config">课程详情</div>
          <div v-show="config">
            <span>课程介绍</span>
            <span @click="toComment">课程大纲</span>
            <span>课程评价</span>
          </div>
        </template>
        <template #right>
          <van-cell @click="showPopup">
            <van-icon name="share" size="25" color="black" />
          </van-cell>
        </template>
      </van-nav-bar>
    </div>
    <!-- 头部 -->
    <van-popup v-model="show">
      <p>分享</p>
      <qriously :value="initQCode" :size="138" />
    </van-popup>
    <!--  -->
    <div class="top">
      <h3>
        {{ arr.title }}
        <van-icon name="star-o" />
      </h3>
      <p class="p1">{{ arr.price | Myprice }}</p>
      <h4>共有{{ arr.total_periods }}课时| {{ arr.sales_num }}人已报名</h4>
      <span>开课时间：2020.03.16 18：30 - 2020.03.22 15：00</span>
    </div>
    <!-- 教师团队 -->
    <div class="team" v-for="(item,index) in fl" :key="index">
      <h3>教学团队</h3>
      <div class="small" v-for>
        <img :src="item.avatar" alt />
      </div>
      <p>{{ item.teacher_name }}</p>
    </div>
    <!-- 课程介绍 -->
    <div class="js">
      <h3>课程介绍</h3>
    </div>
    <!-- 课程大纲 -->
    <div class="dg">
      <h3 ref="comment">课程大纲</h3>
      <ul>
        <li v-for="(v,i) in list">
          <p>
            <a>.</a>
            <span>回放</span>
            {{ v.periods_title }}
          </p>
        </li>
      </ul>
    </div>
    <!-- 切换 -->
    <van-button type="warning"  block>{{ qh | btn }}</van-button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      config: false,
      arr: [],
      fl: [],
      qh: "", //切换
      list: [
        {
          id: 1,
          periods_title: "第一讲，第一课时"
        },
        {
          id: 1,
          periods_title: "第一讲，第二课时"
        },
        {
          id: 1,
          periods_title: "第二讲，第一课时"
        },
        {
          id: 1,
          periods_title: "第二讲，第二课时"
        }
      ],
      nav: [],
      xs: false,
      initQCode: "https://wap.365msmk.com/course-detail?id=100&courseType=2",
      show: false
    };
  },
  mounted() {
    this.xq();
    // 获取滚动条的数据

    window.addEventListener("scroll", this.handleScrollx, true);
    //
    axios
      .post("/api/app/courseChapter", { id: this.$route.query.id })
      .then(msg => {
        // console.log(msg)
        this.list = msg.data.data;
        this.nav = msg.data.data.teachers;
      });
  },
  methods: {
    //  二维码生成
    showPopup() {
      this.show = true;
    },
    onClickLeft() {
      this.$router.push("/kc");
    },
    async xq() {
      let { data: res } = await axios.get(
        `/api/app/courseInfo/basis_id=${this.$route.query.id}&course_type=${this.$route.query.course_type}`
      );
      // console.log(res)
      this.arr = res.data.info;
      this.fl = res.data.teachers;
    },
    // 滚动事件的回调方法
    handleScrollx() {
      // console.log("滚动的高度", window.pageYOffset)
      this.config = window.pageYOffset > 50;

      // conosle.log("距离顶部高度",this.$refs.obtain.getBoundingClientRect().top)
    },
    // 跳到大纲的位置dom元素位置
    toComment() {
      this.$refs.comment.scrollIntoView();
    }
  },
  created() {
    //  初始化回到顶部显示页面
    document.body.scrollTop = document.documentElement.scrollTop = 0;
  },
  filters: {
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    },

    // 按钮过滤器
   
  }
};
</script>

<style lang="scss">
.twoke {
  .van-nav-bar {
    height: 100px;
    .van-nav-bar__arrow {
      font-size: 24px;
    }
    .van-nav-bar__title {
      font-size: 40px;
    }
  }

  // 脱离文档流样式
  .fixedClass {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    height: 100%;
  }
  .top {
    height: 240px;
    background-color: white;
    padding: 10px 30px;
    width: 96%;
    h3 {
      font-size: 30px;
      line-height: 60px;
    }
    .van-icon {
      float: right;
      margin-right: 30px;
      font-size: 50px;
    }
    p {
      color: orangered;
      font-size: 28px;
      line-height: 50px;
    }
    h4 {
      font-size: 24px;
      color: #8c8c8c;
      line-height: 50px;
    }
    span {
      font-size: 24px;
      color: #8c8c8c;
      line-height: 50px;
    }
  }
  .team {
    height: 240px;
    background-color: white;
    padding: 10px 30px;
    width: 96%;
    margin-top: 30px;
    h3 {
      font-size: 30px;
      line-height: 60px;
    }
    .small {
      width: 70px;
      height: 70px;
      margin-top: 20px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    p {
      font-size: 28px;
      line-height: 70px;
    }
  }
  .js {
    height: 120px;
    background: white;
    margin-top: 20px;
    padding: 20px 20px;
    h3 {
      font-size: 30px;
    }
  }
  .dg {
    background: white;
    margin-top: 20px;
    padding: 20px 20px;
    h3 {
      font-size: 30px;
    }
    ul {
      li {
        list-style: square;
        height: 100px;
        border-bottom: 1px solid red;
        padding: 10px 20px;
        p {
          font-size: 26px;
          color: #8c8c8c;
          a {
            font-size: 60px;
            color: orangered;
            margin-right: 20px;
          }
          span {
            display: inline-block;
            background-color: orange;
            color: white;
            font-size: 26px;
            margin-right: 20px;
          }
        }
      }
    }
  }
  .van-button {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 999;
    height: 1rem;
    font-size: 30px;
  }
}
</style>

